import {CourseScheduleJSON} from './api'

interface ZhouCi {
    /**  */
    startWeek: number,
    /**  */
    count: number,
}

interface CourseDateTime {
    /**
     * 
     */
    start: Date,
    /**
     * 
     */
    end: Date,
    /**
     * 
     */
    count: number,
}

export class CourseSchedule {
    /**  */
    jcArr: number[];
    /**  */
    jxbmc: string;
    /**  */
    jxcdmcs: string;
    /**  */
    kcbh: string;
    /**  */
    kcmc: string;
    /**  */
    kcrwdm: string;
    /**  */
    teaxms: string;
    /**  */
    xq: number;
    /**  */
    zcSchedules: ZhouCi[];

    /**
     *  xsgrkbcx!xsAllKbList.action API 
     */
    constructor({jcdm2, jxbmc, jxcdmcs, kcbh, kcmc, kcrwdm, teaxms, xq, zcs}: CourseScheduleJSON) {
        this.jcArr = jcdm2.split(',').map(Number).filter(x => x > 0);
        this.jxbmc = jxbmc;
        this.jxcdmcs = jxcdmcs;
        this.kcbh = kcbh;
        this.kcmc = kcmc;
        this.kcrwdm = kcrwdm;
        this.teaxms = teaxms;
        this.xq = Number(xq);
        this.zcSchedules = zcsToStartWeekAndCount(zcs);
    }

    getCourseSchedulesInDate(firstDayInSemester: Date) {
        const courseSchedules: CourseDateTime[] = [];
        if (this.jcArr.length === 0) {
            console.log('Warning: ' + this.kcmc);
            return courseSchedules;
        }
        for (const zcSchedule of this.zcSchedules) {
            const day = new Date(firstDayInSemester)
            // 
            day.setDate(day.getDate() + ((zcSchedule.startWeek - 1) * 7) + (this.xq - 1))
            courseSchedules.push({
                start: new Date(day.setHours(jcToHourMinute[this.jcArr[0]-1][0][0], jcToHourMinute[this.jcArr[0]-1][0][1])),
                end: new Date(day.setHours(jcToHourMinute[this.jcArr[this.jcArr.length - 1] - 1][1][0], jcToHourMinute[this.jcArr[this.jcArr.length - 1] - 1][1][1])),
                count: zcSchedule.count,
            })
        }
        return courseSchedules;
    }
}

/**
 * 
 * @param zcs API 
 * @returns 
 */
function zcsToStartWeekAndCount(zcs: string) {
    const weeks = Array.from(new Set(zcs.split(',').map(Number))).sort((a, b) => a - b);
    const output:ZhouCi[] = []
    let weekStartIndex = 0
    weeks.forEach((_weekNum, index) => {
        if (index === weeks.length - 1 || weeks[index + 1] !== weeks[index] + 1) {
            output.push({startWeek: weeks[weekStartIndex], count: index - weekStartIndex + 1})
            weekStartIndex = index + 1
        }
    })
    return output
}

/**
 * 
 * : https://www.gdut.edu.cn/info/1900/8883.htm
 */
const jcToHourMinute = [
    [[8,30], [9,15]],
    [[9,20], [10,5]],
    [[10,25], [11,10]],
    [[11,15], [12,0]],
    [[13,50], [14,35]],
    [[14,40], [15,25]],
    [[15,30], [16,15]],
    [[16,30], [17,15]],
    [[17,20], [18,5]],
    [[18,30], [19,15]],
    [[19,20], [20,5]],
    [[20,10], [20,55]],
]
